import React from "react";
import "../css/about/about.css";
import Cards from "../components/Cards";
import TitleComponent from "../components/TitlePage";

const About = () => {
  const calculate_age = (dob) => {
    const birthDate = new Date(dob);
    const difference = Date.now() - birthDate.getTime();
    const age = new Date(difference);
    return Math.abs(age.getUTCFullYear() - 1970);
  };

  const cache = {};
  function importAll(r) {
    r.keys().forEach((key) => (cache[key] = r(key)));
  }
  importAll(require.context("../photos/logo", false, /\.(png|jpe?g|svg)$/));
  const images = { image: {}, text: [] };

  function getImages() {
    Object.entries(cache).forEach(
      (module, index) => (
        (images.image[index] = module[1].default),
        (images.text[index] = module[0].replace("./", ""))
      )
    );
  }
  getImages();

  const img = (imgtext) => {
    return images.image[images.text.indexOf(imgtext)];
  };

  return (
    <div className="page">
      <TitleComponent title="About" />
      <div className="about">
        <h1>Who I am?</h1>

        <p className="text">
          &emsp;My name is Abrudan Titus Daniel and I am{" "}
          {calculate_age("12-4-1999")} years old. I am a motivated junior .NET
          developer with a focus on delivering efficient software solutions.
          Proficient in C#, ASP.NET, and SQL Server, I apply solid
          object-oriented programming principles. I actively seek opportunities
          for growth, staying updated with industry trends and technologies.
          With strong problem-solving skills, I excel in collaborative team
          settings.
        </p>

        <h1>What I using for programming?</h1>

        <div className="cardplace">
          <Cards
            link="c-sharp"
            textlink="See more"
            height="500px"
            image={img("c-sharp.png")}
            title="C-Sharp"
            text="&emsp;In most cases, I have used this language, especially when it comes to web or executable applications. It allows for the use of OOP and has a structured syntax that closely resembles Java."
          />
          <Cards
            link="netcore"
            textlink="See more"
            height="500px"
            image={img("netcore.png")}
            title="Net Core"
            text="&emsp;.NET Core is a web application development framework that I have used alongside the C# language and the Entity Framework relational framework to facilitate the implementation of application backends."
          />
          <Cards
            link="react"
            textlink="See more"
            height="500px"
            image={img("react.png")}
            title="React"
            text="&emsp;Developed by Facebook, React is a JavaScript library that I have used to implement the front-end of various applications. From a syntax perspective, it may seem challenging to use, but the results it delivers make it worthwhile."
          />
          <Cards
            link="arduino"
            textlink="See more"
            height="500px"
            image={img("arduino.png")}
            title="Arduino"
            text="&emsp;Arduino is a platform for developing electronic products. For programming these products, I have utilized the C/C++ language, and the ability to incorporate predefined libraries makes the work easier."
          />
          <Cards
            link="postgresql"
            textlink="See more"
            height="500px"
            image={img("postgresql.png")}
            title="PostgreSQL"
            text="&emsp;For over 30 years, PostgreSQL has been a relational database. I enjoy using it because it provides a good way of organizing data."
          />
          <Cards
            link="mysql"
            textlink="See more"
            height="500px"
            image={img("mysql.png")}
            title="MySQL"
            text="&emsp;MySQL, a relational database, is easy to model and widely used by many companies. I have utilized this database alongside the PHP language to develop small projects."
          />
        </div>

        <h1>Other technologies that attract me...</h1>

        <div className="cardplace">
          <Cards
            height="250px"
            image={img("nodejs.png")}
            title="NodeJS"
            link=""
          />
          <Cards height="250px" image={img("java.png")} title="Java" link="" />
          <Cards
            height="250px"
            image={img("mongodb.png")}
            title="MongoDB"
            link=""
          />
          <Cards
            height="250px"
            image={img("raspberrypi.png")}
            title="Raspberry Pi"
          />
        </div>
      </div>
    </div>
  );
};

export default About;
