import React from "react";
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";
import BusinessCenterIcon from "@material-ui/icons/BusinessCenter";
import TitleComponent from "../components/TitlePage";
import SchoolIcon from "@material-ui/icons/School";
import LocationCity from "@material-ui/icons/LocationCity";
import WorkspacePremiumIcon from "@mui/icons-material/WorkspacePremium";
import "../css/timeline/timeline.css";
import "react-vertical-timeline-component/style.min.css";

const Timeline = () => {
  return (
    <div className="page">
      <TitleComponent title="Timeline" />
      <div className="timeline">
        <VerticalTimeline animate={false}>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            contentStyle={{ background: "#D2B48C", color: "black" }}
            contentArrowStyle={{ borderRight: "7px solid  #D2B48C" }}
            date="2022"
            iconStyle={{ background: "#D2B48C", color: "white" }}
            icon={<WorkspacePremiumIcon />}
          >
            <h3 className="vertical-timeline-element-title">
              Microsoft Certified: Azure Administrator Associate
            </h3>
            <h4 className="vertical-timeline-element-subtitle">
              [Certification]
            </h4>
            <p>
              Earning Azure Administrator Associate certification validates the
              skills and knowledge to implement, manage, and monitor an
              organization’s Microsoft Azure environment.
            </p>
            <a
              href="https://credly.com/badges/0e4eb528-ba11-4400-bf5d-3a30d404f449"
              target="_blank"
            >
              Link for certificate
            </a>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            contentStyle={{ background: "#87CEEB", color: "black" }}
            contentArrowStyle={{ borderRight: "7px solid  #87CEEB" }}
            date="Sept 2022 - July 2023"
            iconStyle={{ background: "#87CEEB", color: "white" }}
            icon={<BusinessCenterIcon />}
          >
            <h3 className="vertical-timeline-element-title">LATERAL</h3>
            <h4 className="vertical-timeline-element-subtitle">[Job]</h4>
            <p>
              The organization where I worked as a junior .NET developer,
              provided an exceptional environment for me to deepen my
              programming skills. It served as a catalyst for my professional
              growth, allowing me to delve into various aspects of software
              development.
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            contentStyle={{ background: "#FF5050", color: "black" }}
            contentArrowStyle={{ borderRight: "7px solid  #FF5050" }}
            date="2019 - 2020"
            iconStyle={{ background: "#FF5050", color: "white" }}
            icon={<BusinessCenterIcon />}
          >
            <h3 className="vertical-timeline-element-title">GUENTHER IMPEX</h3>
            <h4 className="vertical-timeline-element-subtitle">[Practice]</h4>
            <p>
              The place where I completed my practice during university and
              where I was able to develop myself the most and put into practice
              every idea, as well as the ideas of others. Additionally, the
              opportunity to implement production management programs and test
              them on employees for better development.
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            contentStyle={{ background: "#4682B4", color: "black" }}
            contentArrowStyle={{ borderRight: "7px solid  #4682B4" }}
            date="2018 - present"
            iconStyle={{ background: "#4682B4", color: "white" }}
            icon={<SchoolIcon />}
          >
            <h3 className="vertical-timeline-element-title">
              UNIVERSITY OF ORADEA FACULTY OF ELECTRICAL ENGINEERING AND
              INFORMATION TECHNOLOGY
            </h3>
            <h4 className="vertical-timeline-element-subtitle">[College]</h4>
            <p>
              I am studying Computer Science and Electronics in the Information
              Technology profile. I believe that the faculty offers the
              opportunity to experience a wide range of things and open doors to
              new possibilities.
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            contentStyle={{ background: "#6B5B95", color: "black" }}
            contentArrowStyle={{ borderRight: "7px solid  #6B5B95" }}
            date="2014 - 2018"
            iconStyle={{ background: "#6B5B95", color: "white" }}
            icon={<LocationCity />}
          >
            <h3 className="vertical-timeline-element-title">
              National College "Iosif Vulcan"
            </h3>
            <h4 className="vertical-timeline-element-subtitle">
              [High School]
            </h4>
            <p>
              I have successfully completed the Mathematics and Informatics
              profile, which provided me with knowledge in the C/C++ programming
              language and problem-solving techniques for mathematical
              algorithms.
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            contentStyle={{ background: "#708090", color: "black" }}
            contentArrowStyle={{ borderRight: "7px solid  #708090" }}
            date="2006 - 2014"
            iconStyle={{ background: "#708090", color: "white" }}
            icon={<LocationCity />}
          >
            <h3 className="vertical-timeline-element-title">
              Primary School No. 11
            </h3>
            <h4 className="vertical-timeline-element-subtitle">
              [Primary School]
            </h4>
            <p>
              The place where I discovered my passion for computer science and
              acquired fundamental knowledge.
            </p>
          </VerticalTimelineElement>
        </VerticalTimeline>
      </div>
    </div>
  );
};

export default Timeline;
